.srchb {
  height: 0vh;
  width: 70vw;
  background-color: #ccc;
  transition: height 200ms ease-in-out;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  &.open {
    height: 15vh; } }
.openBtnContainer {
  transition: color 200ms ease;
  &:hover {
    color: black; } }
@media screen and ( max-width: 500px ) {
  .srchb {
    width: 100vw;
    &.open {
      height: 10vh; } } }
